import React, { useState } from 'react';
import './App.scss';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

import Localization from './Localization';

import CV from './pages/CV';
import Header from './components/Header';

import { Link } from "react-scroll";

import NightModeIcon from  "@material-ui/icons/Brightness3";

function App() {
  const [language] = useState("fr");
  const [nightMode, setNightMode] = useState(true);
  const [currentPage] = useState({name: "CV"});

  const darkTheme = createMuiTheme({
    typography: {
      fontFamily: [
        'Montserrat',
        'sans-serif',
      ].join(','),
      fontWeightMedium: 700
    },
    palette: {
      type: 'dark',
      primary: {
        main: '#833',
        contrastText: '#fff',
      },
      secondary: {
        main: '#388',
        contrastText: '#fff',
      }
    },
  });

  const lightTheme = createMuiTheme({
    typography: {
      fontFamily: [
        'Montserrat',
        'sans-serif',
      ].join(','),
      fontWeightMedium: 700
    },
    palette: {
      type: 'light',
      primary: {
        main: '#8cc',
        contrastText: '#000',
      },
      secondary: {
        main: '#cc8',
        contrastText: '#000',
      }
    },
  });


  function renderPage() {
    switch(currentPage.name) {
      case "CV":
        return <CV />
      default:
        return <CV />
    }
  }

  function headerLinks(language, nightMode) {
    return [
      // {
      //   action: () => setLanguage(language === "fr" ? "en" : "fr"),
      //   component: <span>{language === "fr" ? "English" : "Français"}</span>,
      //   icon: <LanguageIcon />
      // },
      {
        action: () => setNightMode(!nightMode),
        component: <span>{nightMode ? "Day mode" : "Night mode"}</span>,
        icon: <NightModeIcon />
      }
    ];
  }

  function headerSubLinks() {
    return [
      {action: () => {}, component: <Link spy={true} smooth={true} duration={500} to="skills-nav">Compétences</Link>},
      {action: () => {}, component: <Link spy={true} smooth={true} duration={500} to="projects-nav">Projets</Link>},
      {action: () => {}, component: <Link spy={true} smooth={true} duration={500} to="formation-nav">Formation</Link>},
      {action: () => {}, component: <Link spy={true} smooth={true} duration={500} to="languages-nav">Langues</Link>}
    ]
  }
  
  return (
    <Localization.Provider value={language}>
      <ThemeProvider theme={nightMode ? darkTheme : lightTheme}>
        <div className={"theme " + (nightMode ? "theme--dark" : "theme--light")}>
          <div id="App">
            <Header title="CV" subHeader={true} sticky={true}
              links={headerLinks(language, nightMode)}
              subLinks={headerSubLinks()} />
            {renderPage()}
            <footer id="footer">
              <p>Site réalisé en React JS</p>
            </footer>
          </div>
        </div>
      </ThemeProvider>
    </Localization.Provider>
  );
}

export default App;
