import React, { useState, useEffect } from 'react';
import { ListSubheader, ListItem, ListItemText } from '@material-ui/core';
import styled, {keyframes} from 'styled-components';

function createSkillBar(size, color, i) {
    // const [anim, setAnim] = useState("");

    // useEffect(() => {
    //     window.setTimeout(() => {
    //         setAnim("skill-anim");
    //     }, 500 * i)
    // }, [setAnim, i])

    

    const skillAnim = keyframes`
        0% {
            width: 0px;
        }
        100% {
            width: ${(size * 8) + "%"};
        }
    `;
    return styled.div`
        position: absolute;
        height: 50px;
        background-color: ${color};
        z-index: -10;
        &&.skill-anim {
            animation: ${skillAnim} 2s forwards cubic-bezier(0.445, 0.05, 0.55, 0.95);
        }
    `;
    // return <SkillDiv className={anim} style={{position: "absolute", height: "50px", backgroundColor: color, zIndex: "-10"}}  />
}

function Skills(props) {
    
    const data = props.data;
    const [skillBars, setSkillBars] = useState([]);
    const [anims, setAnims] = useState([]);

    useEffect(() => {
        let _skillBars = [];

        for (let i = 0; i < data.length; ++i) {
            _skillBars.push(createSkillBar(data[i].affinity, data[i].color, i));
        }

        setSkillBars(_skillBars);

        function animateSkillBars(i = 0) {
            window.setTimeout(() => {
                setAnims(anims => ([...anims, "skill-anim"]));
                if (++i < data.length)
                    animateSkillBars(i);
            }, 750);
        }
        animateSkillBars();
    }, [data])

    

    if (skillBars.length === data.length)
    return (<div id="skills">
        <h3>Compétences</h3>
        {/* <h2>Les barres représentent mon degré d'affinité avec le langage</h2> */}
        <div id="cv-skills" className="cv-section">
            {data.map((language, i) => {
                const SkillBar = skillBars[i];
                return <ul key={i} style={{listStyle: "none"}}>
                        <SkillBar className={anims[i]} />
                        <ListSubheader disableSticky className="cv-skill">{language.name}</ListSubheader>
                        {language.frameworks.map((item, j) => 
                            <ListItem key={j}>
                                <ListItemText className="cv-subskill" primary={item} />
                            </ListItem>
                        )}
                </ul>
            })}
        </div>
    </div>)
    return <p>Loading</p>

}

export default Skills;