import React from 'react';

function Bio(props) {
    
    const data = props.data;

    if (data)
        return (
            <div id="cv-info">
                <img id="cv-pic" src={data.picture} alt="Sylvain Bédène" />
                <div>
                    <p>{data.firstName} {data.lastName}</p>
                    <p>{data.birth.toLocaleDateString()} {"(" + (new Date(new Date() - data.birth).getFullYear() - 1970) + " ans)"}</p>
                    <p id="cv-bio">{data.title}</p>
                </div>
                <div id="cv-contact">
                    {data.contact.map((contact, i) => 
                        <div key={i} className="bio-contact-container" style={{display: "flex", flexDirection: "row", alignItems: "center"}}>
                            {contact.icon}
                            <a href={contact.link}>{contact.value}</a>
                        </div>
                    )}
                </div>
            </div>
        )
}

export default Bio;