import React from 'react';

function Languages(props) {
    
    const data = props.data;

    return (<div className="fade-anim" id="languages">
        <h3>Langues</h3>
        <div id="cv-languages" className="cv-section">
            {data.map((language, i) => 
                <div key={i}>
                    <p>{language.name}</p>
                    <p className="language-level">{language.level}</p>
                </div>
            )}
        </div>
    </div>)

}

export default Languages;