import React, { useEffect, useState } from 'react';
import styled from 'styled-components';

function createBackground(projectImage) {
    const backgroundImage = projectImage;

    return styled.div`
        &::before {
            content: "";
            top: 0;
            left: 5%;
            width: 90%;
            margin: auto;
            height: 125px;
            position: absolute;
            background-image: url(${backgroundImage});
            background-size: cover;
            background-position-y: center;
            filter: blur(1px) grayscale(0.8) brightness(0.6);
            border-radius: 10px;
        }
    `;
}

function Projects(props) {

    const data = props.data;
    const [backgrounds, setBackgrounds] = useState([]);
    
    useEffect(() => {
        let _backgrounds = [];
        data.forEach((project) => {
            _backgrounds.push(createBackground(project.image));
        });
        setBackgrounds(_backgrounds);
    }, [setBackgrounds, data])

    if (backgrounds.length === data.length)
    return (<div id="projects">
        <h3>Projets</h3>
        {data.map((project, i) => {
            const Background = backgrounds[i];
            return <div key={i} className="project">
                <Background className="project-image">
                    <p className="project-image-inside">{project.name}</p>
                </Background>
                <div className="project-language-container">
                    {project.languages.map((language, j) => 
                        <p key={j} style={{marginLeft: "5px", marginRight: "5px"}}>{language}</p>
                    )}
                </div>
                <p className="project-description">{project.description}</p>
            </div>
        })}
    </div>)
    return <p>Loading</p>

}

export default Projects;