import React, { useState, useEffect, useContext } from 'react';

import './cv.scss';
import styled, {keyframes} from 'styled-components';

import API from '../API/API';
import Description from '../components/Description';
import Formation from '../components/Formation';
import Languages from '../components/Languages';
import Skills from '../components/Skills';
import Bio from '../components/Bio';
import Localization from '../Localization';
import Projects from '../components/Projects';

const cvAnim = keyframes`
    0% {
        transform: translateY(-100px);
        opacity: 0%;
    }
    100% {
        transform: translateY(0px);
        opacity: 100%;
    }
`;

const CvDiv = styled.div`
    &&.cv-anim {
        animation: ${cvAnim} 1s forwards cubic-bezier(0.445, 0.05, 0.55, 0.95);;
    }
`;

function CV() {

    const [cvInfo, setCvInfo] = useState(null);

    const localization = useContext(Localization);

    useEffect(() => {
        setCvInfo(API.fetchCvInfo(localization));
    }, [localization])

    if (cvInfo)
        return (<>
            
            <CvDiv id="cv-wrapper" className="cv-anim">

                <Bio data={cvInfo.bio} />

                <Description data={cvInfo.description} />

                <div className="hr" id="skills-nav" />

                <Skills data={cvInfo.skills} />
                
                <div className="hr" id="projects-nav" />

                <Projects data={cvInfo.projects} />

                <div className="hr" id="formation-nav" />

                <Formation data={cvInfo.formation} />

                <div className="hr" id="languages-nav" />

                <Languages data={cvInfo.languages} />
                    
            </CvDiv>
        </>);
    return <p>Loading</p>

}

export default CV;