import React from 'react';
import Timeline from './Timeline';

function Formation(props) {

    const data = props.data;

    return (<div className="fade-anim" id="formation">
        <h3>Formation</h3>
        <div id="cv-formation" className="cv-section">
            <Timeline data={data} />
        </div>
    </div>)

}

export default Formation;