import React from 'react';
import "./header.scss";
import shortid from "shortid";
import { Link } from 'react-scroll';
// import Button from 'react-bootstrap/Button';

class Header extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            isLogged: localStorage.userToken ? true : false,
            subHeader: this.props.subHeader ? true : false,
            sticky: this.props.sticky ? true : false,
            yOffset: 0,
            direction: 0,
            appearedOnce: false,
            subAppearedOnce: false,
            links: this.props.links ? this.props.links.map(link => <div className="header-link" onClick={link.action} key={shortid.generate()}>{link.component}</div>) : null,
            subLinks: this.props.subLinks ? this.props.subLinks.map(subLink => <div className="header-sub-link" onClick={subLink.action} key={shortid.generate()}>{subLink.component}</div>) : null
        };
    }

    componentDidMount() {
        window.addEventListener('scroll', this.listenToScroll)
    }

    componentWillUnmount() {
        window.removeEventListener("scroll", this.listenToScroll);
    }

    listenToScroll = () => {
        var oldOffset = this.state.yOffset;
        this.setState({
            yOffset: window.pageYOffset,
        })
        if (this.state.yOffset > 125)
            this.setState({appearedOnce: true});
        this.setState({direction: (this.state.yOffset < oldOffset ? -1 : 1)})
        if (this.state.direction === -1 && this.state.yOffset > 125)
            this.setState({subAppearedOnce: true})
    }

    subHeader() {
        if (this.state.subHeader)
            return (
                <div className="subHeader">
                    <div className="subLinks">
                        {this.state.subLinks}
                    </div>
                </div>
            );
    }

    stickySubHeader() {
        if (this.state.subHeader && this.state.subAppearedOnce)
        {
            return (
                <div className={"stickySubHeader " + (this.state.direction === -1 && this.state.yOffset > 125 ? "fade-in-80" : "fade-out-80")}>
                    <div className="subLinks">
                        {this.state.subLinks}
                    </div>
                </div>
            );
        }
    }

    stickyHeader() {
        if (this.state.sticky && this.state.appearedOnce)
            return (
                <>
                    <div className={"stickyHeader " + (this.state.yOffset > 125 ? "fade-in-40" : "fade-out-40")}>
                        <Link smooth={true} duration={500} to="App">
                            <span id="stickyTitle">{this.props.title ? this.props.title : ""}</span>
                        </Link>
                        <div id="sticky-header-icons">
                            {this.props.links.map((link, i) => <div key={i} className="sticky-header-icon" onClick={link.action}>{link.icon}</div>)}
                        </div>
                    </div>
                    {this.stickySubHeader()}
                </>
            );
    }

    render() {
        return (
            <>
                <div className="header">
                    <Link smooth={true} duration={500} to="App">
                        <span id="title">{this.props.title ? this.props.title : ""}</span>
                    </Link>
                    <div className="links">
                        {this.props.links.map((link)=> <div className="header-link" onClick={link.action} key={shortid.generate()}>{link.component}</div>)}
                    </div>
                </div>
                {this.subHeader()}
                {this.stickyHeader()}
            </>
        )
    }
}

export default Header;